	module.exports = {"data":{"markdownRemark":{"html":"<!--当サイトは、ほとんどのモダンなWebブラウザで表示確認をしております。この記事では表示可能なWebブラウザ一覧を列挙しています。-->\n<p>当サイトは、下記のWebブラウザにおいて、表示テストを行っております。</p>\n<h2>◆完全に対応しているWebブラウザ一覧</h2>\n<ul>\n<li>\n<p><a href=\"http://windows.microsoft.com/ja-JP/internet-explorer/download-ie\">Microsoft Internet Explorer</a> 10、11[10以降はModern UI版含む]</p>\n</li>\n<li>\n<p><a href=\"http://jp.opera.com/\">Opera</a> 28</p>\n</li>\n<li>\n<p><a href=\"http://support.apple.com/kb/DL1531?viewlocale=ja_JP\">Apple Safari</a> 8</p>\n</li>\n<li>\n<p><a href=\"http://www.google.co.jp/intl/ja/chrome/browser/\">Google Chrome</a> 41.0 [Windows版・Linux版]</p>\n</li>\n<li>\n<p><a href=\"http://www.mozilla.jp/firefox/\">Mozilla Firefox</a> 37 [Windows版]</p>\n<h2>◆ほぼ問題ないレベルで対応しているWebブラウザ一覧</h2>\n</li>\n<li>\n<p>PSVITA ブラウザー (ごく一部に非対応コンテンツがあります)</p>\n</li>\n<li>\n<p><a href=\"http://windows.microsoft.com/ja-JP/internet-explorer/download-ie\">Microsoft Internet Explorer</a> 9<a href=\"%E4%B8%80%E9%83%A8%E8%A1%A8%E7%A4%BA%E3%81%8C%E3%81%8A%E3%81%8B%E3%81%97%E3%81%84%E3%81%A7%E3%81%99\">WindowsPhone7.5-7.8、XBOX360含む</a></p>\n<h2>◆最低限読める程度には対応しているWebブラウザ一覧</h2>\n</li>\n<li>\n<p><a href=\"http://www.microsoft.com/ja-jp/windows/ie/downloads/ie6sp1/default.aspx\">Microsoft Internet Explorer</a> ～8 (所々大きくレイアウト崩れがあります。基本サポート対象外とさせて戴きますが、とりあえず読める程度には対応しているつもりです)</p>\n</li>\n<li>\n<p><a href=\"http://w3m.sourceforge.net/\">w3m</a> 8.5.2 <a href=\"%E3%83%86%E3%82%AD%E3%82%B9%E3%83%88%E3%83%96%E3%83%A9%E3%82%A6%E3%82%B6%E3%81%A7%E3%81%99%E3%81%8C%E3%80%81%E3%81%A1%E3%82%83%E3%82%93%E3%81%A8%E8%A8%98%E4%BA%8B%E8%AA%AD%E3%82%81%E3%81%BE%E3%81%99\">Linux版</a></p>\n</li>\n<li>\n<p><a href=\"http://lynx.browser.org/\">lynx</a> 2.8 <a href=\"%E4%BB%A5%E4%B8%8B%E5%90%8C%E6%96%87\">Linux版</a></p>\n</li>\n<li>\n<p><a href=\"http://elinks.or.cz/\">ELinks</a> 0.12 pre5 <a href=\"%E4%BB%A5%E4%B8%8B%E5%90%8C%E6%96%87\">Linux版</a></p>\n</li>\n<li>\n<p><a href=\"http://www.android.com/\">Android</a>ブラウザ [ChromeはOK]</p>\n<h2>◆もうだめぽなWebブラウザ一覧</h2>\n</li>\n<li>\n<p><a href=\"http://www.w3.org/Amaya/\">amaya</a> 11.4.4 (Javascriptがすっこけるなど、現状ロクなことにならない……てかこのブラウザ、まだ活動してたんだ)</p>\n</li>\n<li>\n<p><a href=\"http://www.jikos.cz/~mikulas/links/\">Links</a> 2.2 (激しく文字化け)</p>\n</li>\n<li>\n<p><a href=\"http://elinks.or.cz/\">ELinks</a> 0.11.6、0.12pre5 <a href=\"%E6%BF%80%E3%81%97%E3%81%8F%E6%96%87%E5%AD%97%E5%8C%96%E3%81%91\">Windows向けビルド</a>\nもしこのブラウザだとちゃんと表示されたよー、とか、一部表示乱れてるけど一応読めたよーとか、これじゃ読めないよーとかあれば、該当するブラウザ名とバージョン情報をコメント残していただけると大変ありがたいです。 <a href=\"http://k-tai.hitachi.jp/pc/lineup.html\">au WIN W43H</a>でもちゃんと表示されるかどうか、テストしたいところなのですが、端末をどこかに失くしてしまったため、まだテストしていません。(部屋のどこかにあることは間違いないのですが……) ちなみに、このWebページはページにもよりますが、<a href=\"http://www.htmllint.net/\">Another HTML-lint 5</a>にて85～91点を取得しています。(満点とか相当むりぽ)</p>\n</li>\n</ul>","frontmatter":{"cover":null,"date":"2012-11-27T23:20:39+09:00","strDate":"2012/11/27","title":"Webブラウザ対応状況"}}},"pathContext":{"path":"/ja/blog/2012/11/27-browser/","langKey":"ja"}}

